.container {
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-image: url('../../assets/background.jpg');
    /* background-color: black; */
    background-size: contain;
    background-blend-mode: darken;
    display: flex;
    justify-content: center;
    align-items: center;
}

.img{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
}
.wrapper {
    height: 75vh;
    width: 27.5vw;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    background-color: #fff;
}

.wrapper>h2 {
    color: #171097;
    text-align: center;
    margin: 2.5rem 0;
    font-size: 36px;
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif
}

form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 42px;
}

form>input {
    width: 50%;
    outline: none;
    border: none;
    border-bottom: 1px solid #0b0a0a;
}

form>label {
    align-self: center;
    width: 50%;
    cursor: pointer;
    transition: 150ms all;
    display: flex;
    align-items: center;
    gap: 12px;
}

form>label:hover {
    color: #444;
}

form>button {
    outline: none;
    background-color: blue;
    color: #fff;
    border-radius: 12px;
    padding: 0.5rem 1.25rem;
    font-size: 18px;
    cursor: pointer;
    border: 1px solid transparent;
    margin-top: 1rem;
}

form>button:hover {
    background-color: #fff;
    border-color: blue;
    color: blue;
}

form>p {
    text-align: center;
    color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

form>p>a {
    text-decoration: none;
    font-size: 17px;
    color: #000000;
    margin-top: 0.25rem;
}

.error {
    height: 70px;
    width: 250px;
    text-align: center;
    border-radius: 20px;
    line-height: 1.5rem;
    padding: 0.75rem 1.5rem;
    background-color: #f00;
    color: #fff;
    position: fixed;
    top: 5rem;
    right: 5rem;
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* RESPONSIVE */

@media only screen and (max-width: 992px) {
    .wrapper {
        height: 60vh;
        width: 30vw;
    }
}


@media only screen and (max-width: 768px) {
    .wrapper {
        height: 65vh;
        width: 40vw;
    }
}


.googlebutton {
    border-radius: 20px;
    box-sizing: border-box;
    margin: 20px 10px 20px 10px;
    padding: 15px 15px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
          rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
          "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
    font-size: 20px;
    gap: 5px;
  }
